@import url(https://fonts.googleapis.com/css2?family=Gilda+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  font-family: 'Gilda Display', serif;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.main{
    width: 100%;
    height: 100vh;
    background: url(/tourism_website/static/media/main.cfb91ad4.jpg);
    background-position: center;
    background-attachment: fixed;
    background-size: cover;
    /* margin-top: -30px; */
}

.main::after{
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.74), rgba(0, 0, 0, 0.103));
    position: absolute;
    bottom: 0;
}

.mainTitle{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #fff;
    width: 100%;
    z-index: 1;
}

.mainTitle .title{
    margin-top: 50px;
    margin-bottom: 20px;
    font-size: 50px;
    letter-spacing: 30px;
    text-shadow: 1px 1px 10px rgb(78, 78, 78);
}

.mainTitle .p1{
    font-size: 75px;
    letter-spacing: 18px;
    text-shadow: 1px 1px 10px rgb(78, 78, 78);
}

.mainTitle .p2{
    font-size: 20px;
    letter-spacing: 5px;
    text-shadow: 1px 1px 10px rgb(78, 78, 78);
}

.mainTitle .btn-read{
    background: none;
    width: 150px;
    height: 30px;
    color: white;
    border: 1px solid #fff;
    font-size: 11px;
    letter-spacing: 3px;
    margin-top: 60px;
    cursor: pointer;
    outline: none;
}

.mainTitle .btn-read:hover{
    background-color: #fff;
    color: #000;
    transition: ease-in .5s;
}

@media only screen and (max-width: 768px){
    .mainTitle .title{
        font-size: 1.5rem;
        /* margin-bottom: -3px; */
        letter-spacing: 1rem;
    }
    .mainTitle .p1{
        font-size: 3.2rem;
    }
    
    .mainTitle .p2{
        font-size: 20px;
        letter-spacing: 5px;
        text-shadow: 1px 1px 10px rgb(78, 78, 78);
    }
}

nav{
    height: 100px;
    width: 90%;
    /* background-color: aquamarine; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 12px;
    color: #0f0f0f;
    position: fixed;
    z-index: 2;
    margin: 0 5%;
}

nav .menu{
    font-size: 80px;
    text-decoration: none;
    color: #030303;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    color: #030303;
}

.nav-menu {
    background-color: rgba(255, 255, 255, .9);
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 99;
    padding: 100px 60px;
}

.nav-menu.active{
    left: 0;
    transition: 1s;
}

.nav-text{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    margin-bottom: 50px;
}

.nav-text a {
    text-decoration: none;
    color: #030303;
    font-size: 35px;
    width: 20%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 16px;
}

.nav-text a:hover{
    border-bottom: 1px solid #000;
}

.nav-menu-items{
    width: 100%;
}

.navbar-toggle{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    /* background-color: yellow; */
    margin-top: -80px;
}

li{
    list-style: none;
}

nav .book{
    border: 1px solid #030303;
    padding: 5px 10px;
    letter-spacing: 2px;
    cursor: pointer;
}

nav .book:hover{
    background-color: #fff;
    color: #000;
    transition: ease-in .5s;
}


