.view{
    width: 100%;
    height: 120vh;
    background-color: rgb(241, 241, 241);
    /* margin-top: -20px; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.view .viewText{
    width: 60%;
    height: 600px;
    background-color: rgb(211, 211, 211);
    display: flex;
    margin-left: 200px;
    margin-top: 170px;
}

.view .viewText img{
    width: 35%;
    position: absolute;
    margin-top: -100px;
    margin-left: -150px;
}

.viewText .textView{
    width: 37%;
    margin-left: 50%;
    margin-top: 80px;
    text-align: left;
}

.textView h1{
    font-size: 50px;
    color: #4e4e4e;
    margin-bottom: 30px;
    font-family: 'Playfair Display', serif;
    font-weight: 100;
}

@media only screen and (max-width : 768px){
    .view .viewText{
        width: 100%;
        height: 100%;
        background-color: #fff;
        display: flex;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        margin-left: 0;
        margin-top: 0;
        position: relative;
    }
    .view .viewText img{
        width: 100%;
        position: relative;
        margin-top: 0;
        margin-left: 0;
        margin-bottom: rem;
        padding: 1rem;
    }
    
    .viewText .textView{
        width: 100%;
        margin-left: 0;
        margin-top: 0;
        text-align: left;
        padding: 1rem;
    }
}
.paragliding{
    width: 100%;
    height: 120vh;
    background-color: rgb(241, 241, 241);
    /* margin-top: -20px; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.paragliding .paraglidingText{
    width: 80%;
    height: 700px;
    background: url(/tourism_website/static/media/paragliding5.51edc0c7.jpg);
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    /* margin-left: 200px; */
    /* margin-top: 170px; */
}

.paraglidingText::after{
    content: "";
    display: inline-block;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.74), rgba(0, 0, 0, 0.103));
    position: relative;

}


.paraglidingText .textParagliding{
    width: 40%;
    margin-top: 160px;
    margin-left: 100px;
    text-align: left;
    color: #fff;
    position: absolute;
    font-size: 18px;
    z-index: 2;
}

.textParagliding h1{
    font-size: 60px;
    color: #f3f3f3;
    margin-bottom: 30px;
    font-family: 'Playfair Display', serif;
    font-weight: 100;
}

@media only screen and (max-width: 768px){
    .paragliding .paraglidingText{
        width: 100%;
        height: 700px;
    }
    .paraglidingText .textParagliding{
        width: 100%;
        margin-top: 10rem;
        margin-left: 0; 
        padding: 1rem;
        text-align: center;
        color: #fff;
        position: absolute;
        font-size: 1.2rem;
        z-index: 2;
    }
    
    .textParagliding h1{
        font-size: 3.4rem;
        color: #f3f3f3;
        margin-bottom: 2rem;
    }
}
.comments{
    width: 100%;
    height: 100%;
    padding: 7rem 0;
    background-color: rgb(241, 241, 241);
    display: flex;
    align-items: center;
    justify-content: center;
}

.comments .commentsContainer {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 0;
}

.commentsContainer .comment{
    font-size: 25px;
    margin-bottom: 20px;
}

.commentsContainer .navigator .circle{
    width: 12px;
    height: 12px;
    background-color: rgb(172, 172, 172);
    border-radius: 50%;
    cursor: pointer;
}

.navigator{
    /* background-color: violet; */
    display: flex;
    width: 20%;
    justify-content: space-around;
    margin-top: 30px;
}

@media only screen and (max-width: 768px){
    .comments{
        width: 100%;
        height: 100%;
        padding: 1rem 0;
    }
}
.contact{
